'use client';

import { useState, useEffect } from 'react';
import { useSession } from 'next-auth/react';
import { useRouter } from 'next/navigation';
import { useLanguage } from '@/providers/language-provider';
import { useTheme } from '@/providers/theme-provider';
import {
  AppBar,
  Box,
  CssBaseline,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Toolbar,
  Typography,
  ListItemButton,
  Skeleton,
  Avatar,
} from '@mui/material';
import {
  Menu as MenuIcon,
  Dashboard as DashboardIcon,
  AccountBalance as AccountBalanceIcon,
  Receipt as ReceiptIcon,
  Category as CategoryIcon,
  Savings as SavingsIcon,
  Timeline as TimelineIcon,
  Settings as SettingsIcon,
  Logout as LogoutIcon,
} from '@mui/icons-material';

const drawerWidth = 240;

// Define menu items outside the component to ensure consistency
const menuItemsData = [
  { key: 'dashboard', icon: <DashboardIcon />, path: '/' },
  { key: 'accounts', icon: <AccountBalanceIcon />, path: '/accounts' },
  { key: 'transactions', icon: <ReceiptIcon />, path: '/transactions' },
  { key: 'categories', icon: <CategoryIcon />, path: '/categories' },
  { key: 'budgets', icon: <SavingsIcon />, path: '/budgets' },
  { key: 'goals', icon: <TimelineIcon />, path: '/goals' },
  { key: 'settings', icon: <SettingsIcon />, path: '/settings' },
];

// Common styles to remove outlines
const noOutlineStyle = {
  outline: 'none',
  boxShadow: 'none',
  '&:focus': { outline: 'none' },
  '&:hover': { outline: 'none' },
};

export default function DashboardLayout({ children }: { children: React.ReactNode }) {
  const [mobileOpen, setMobileOpen] = useState(false);
  const [currentPath, setCurrentPath] = useState('');
  const { data: session } = useSession();
  const router = useRouter();
  const { t, isClientReady } = useLanguage();
  // Use the theme context which now always has a value
  const { theme } = useTheme();

  // Log theme value for debugging
  useEffect(() => {
    console.log('DashboardLayout mounted, theme:', theme);
  }, [theme]);

  useEffect(() => {
    // Set the current path on the client side
    setCurrentPath(window.location.pathname);
  }, []);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  // Menu item component that handles server/client rendering safely
  const MenuItem = ({ item }: { item: typeof menuItemsData[0] }) => {
    const isActive = currentPath === item.path || 
                     (item.path !== '/' && currentPath.startsWith(item.path));
    
    return (
      <ListItem key={item.key} disablePadding>
        <ListItemButton 
          onClick={() => router.push(item.path)}
          className={isActive ? 'active' : ''}
          disableRipple
          disableTouchRipple
          sx={noOutlineStyle}
        >
          <ListItemIcon>{item.icon}</ListItemIcon>
          {isClientReady ? (
            <ListItemText primary={t(item.key)} />
          ) : (
            <ListItemText primary={item.key} />
          )}
        </ListItemButton>
      </ListItem>
    );
  };

  const drawer = (
    <div>
      <Toolbar>
        <Typography variant="h6" noWrap component="div">
          CoinKeeper
        </Typography>
      </Toolbar>
      <List>
        {menuItemsData.map((item) => (
          <MenuItem key={item.key} item={item} />
        ))}
        <ListItem disablePadding>
          <ListItemButton 
            onClick={() => router.push('/api/auth/signout')}
            disableRipple
            disableTouchRipple
            sx={noOutlineStyle}
          >
            <ListItemIcon><LogoutIcon /></ListItemIcon>
            {isClientReady ? (
              <ListItemText primary={t('logout')} />
            ) : (
              <ListItemText primary="logout" />
            )}
          </ListItemButton>
        </ListItem>
      </List>
    </div>
  );

  const drawerPaperStyle = {
    boxSizing: 'border-box',
    width: drawerWidth,
    bgcolor: 'background.paper',
    outline: 'none',
    border: 'none',
  };

  return (
    <Box 
      sx={{ display: 'flex', minHeight: '100vh' }}
      className="dashboard-layout"
      data-theme={theme}
    >
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{
          width: { sm: `calc(100% - ${drawerWidth}px)` },
          ml: { sm: drawerWidth },
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: 'none' } }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap component="div">
            <Avatar 
              sx={{ 
                width: 32, 
                height: 32, 
                bgcolor: 'primary.main',
                fontSize: '0.9rem',
                fontWeight: 'bold',
                marginRight: '8px'
              }}
            >
              {session?.user?.username?.charAt(0).toUpperCase() || '?'}
            </Avatar>
            {t('welcome')}, {session?.user?.username}
          </Typography>
          <Box sx={{ flexGrow: 1 }} />
          <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
            {new Date().toLocaleDateString()}
          </Box>
        </Toolbar>
      </AppBar>
      <Box
        component="nav"
        sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
      >
        <Drawer
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true,
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': drawerPaperStyle,
          }}
          className="sidebar"
        >
          {drawer}
        </Drawer>
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: 'none', sm: 'block' },
            '& .MuiDrawer-paper': drawerPaperStyle,
          }}
          className="sidebar"
          open
        >
          {drawer}
        </Drawer>
      </Box>
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          width: { sm: `calc(100% - ${drawerWidth}px)` },
        }}
      >
        <Toolbar />
        {children}
      </Box>
    </Box>
  );
} 